@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Edu+SA+Beginner&family=Ysabeau+SC:wght@800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Oleo+Script:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caveat&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
  max-width: 100vw;
  min-width: 100vw;
}

body {
  background-color: #34435c;
  /* background-color: rgba(93, 42, 94, 0.75); */
}

.hero {
  background-color: #10316b;
  background-image: url(../../../public/images/portfolio_bg.png);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  image-rendering: pixelated;
  text-align: center;
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  top: 0px;
}

.hero > .container {
  max-height: 50%;
  justify-content: center;
  padding-top: 5%;
}

.hero-h1 {
  color: white;
  margin: 15px;
  font-family: "Outfit", sans-serif;
  font-weight: bold;
  text-align: right;
}

.hero-h3 {
  color: #f2b7d3;
  margin: 15px;
  font-family: "Outfit", sans-serif;
  line-height: 0.75;
  text-align: right;
}

.hero-description {
  margin-top: 10%;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  text-align: left;
  border-radius: 10px;
  padding: 10px;
  background-color: rgba(168, 151, 151, 0.25);
}

.ghost-button {
  background-color: rgb(10, 59, 141, 0.5);
  display: inline-block;
  width: 200px;
  padding: 10px;
  color: #fff;
  border: 2px solid #fff;
  text-align: center;
  outline: none;
  text-decoration: none;
  border-radius: 5px;
  content: "view";
  cursor: pointer;
  transition: all ease 0.3s;

  &:hover {
    text-indent: -10px;
    background-color: rgba(255, 255, 255, 0.25);
    transition: all ease 0.5s;
  }
  &:hover:after {
    content: " ðŸ”½";
  }
}

.projects {
  min-height: 100vh;
  min-width: 100vw;
  color: black;
  /* background-color: rgba(25, 33, 49, 0.75); */
  background: linear-gradient(
    0deg,
    rgba(52, 67, 92, 1) 0%,
    rgba(242, 183, 211, 1) 80%
  );

  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

.projects-h1 {
  margin: 0px;
  font-family: "Outfit", sans-serif;
  color: #291414;
  font-weight: bold;
}

.projects-h1-p {
  background-color: rgba(25, 33, 49, 0.75);
  color: #f2b7d3;
  border-radius: 5px;
  font-family: "Outfit", sans-serif;
  font-size: calc(40px + 2vmin);

}

.projects-detail {
  flex-direction: column;
  background-color: transparent;
  background-color: rgba(25, 33, 49, 0.75);
  border-radius: 5px;
  /* border-image: url(../../../public/images/borderxl.png) fill
  48 16 16 16/  48px 16px 16px 16px;
  border-style: solid;
  border-top: 48px solid;
  border-left: 16px solid;
  border-right: 16px solid;
  border-bottom: 16px solid; */

  image-rendering: pixelated;
  color: rgb(255, 235, 235);

  padding: 20px;
  font-size: 18px;
  font-family: "Poppins", sans-serif;
  word-break: break-word;
}
.projects-detail > h3 {
  padding: 0px;
  font-size: 36px;
  color: #f2b7d3;
}
.projects-detail > p {
  text-align: left;
  font-size: 22px;
  padding-left: 5%;
  padding-right: 5%;
}

.project-image {
  width: 50%;
  height: auto;
  image-rendering: pixelated;
}

.tech {
  border: 2px solid white;
  padding: 5px;
  border-radius: 5px;
  margin-bottom: 15%;
  font-size: 16px;
  flex-direction: row;
  width: 100%;

  &:hover {
    background-color: rgb(255, 235, 235);
    color: black;
    font-size: 20px;
    transition: all ease-in-out 0.5s;
  }
}

.projects-link {
  color: #8cf3c4;
  margin: 1%;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  border: #8cf3c4 2px solid;

  &:hover {
    background-color: #8cf3c4;
    color: black;
    font-weight: bold;

    transition: all ease-in-out 0.5s;
  }
}

.about {
  background: rgb(52, 67, 92);

  padding: 5%;

  color: white;
  font-family: "Poppins", sans-serif;
  text-align: left;
  flex-direction: row;
  font-size: calc(7px + 2vmin);
}

.babyalice {
  width: 100%;
  height: 100%;
  padding-top: 20%;
  padding-bottom: 10%;
}

.contact-wrapper {
  min-width: 100vw;
  padding: 0px;
  margin: -20px;
  background-color: #34435c;
}
.contact {
  margin: 5px;
  padding: 20px;
  background-color: rgba(25, 33, 49, 0.75);
  color: white;
  border-radius: 10px;
}

.sparkles {
  text-align: center;
}

.sparkles > img {
  width: 175px;
  height: auto;
  image-rendering: pixelated;
  padding: 5px;
}

.contact > h1 {
  margin: 0px;
}

label,
input,
textarea {
  color: white;
  font-family: "Outfit", Helvetica, Arial;
  font-size: 20px;
}

.input-field input[type="text"].valid {
  border-bottom: 1px solid white;
  box-shadow: 0 1px 0 0 white;
}
::placeholder {
  color: grey;
}

.nav-class {
  display: flex;
  justify-content: center;
}

.nav {
  display: flex;
  background-color: rgba(25, 33, 49, 0.75);
  justify-content: space-around;
  width: 30%;
  margin: auto;
  position: fixed;
  top: 0;
  border-radius: 10px;
}

.about-inner {
  padding: 20px;
  background-color: rgba(25, 33, 49, 0.35);
  border-radius: 10px;
}

.submit {
  color: black;
  padding: 10px;
}

.sm-icons > a > img {
  width: 100px;
  padding: 10px;
  height: auto;
  image-rendering: pixelated;
}

@media screen and (max-width: 600px) {
  .hero-description {
    margin-top: 5%;
  }
  .nav {
    width: 80%;
  }
}
